.message-input {
    width: 100%;
    max-width: 1130px;
    margin: 20px 0;
    margin-right: 15px;
    padding: 23px;
    background: #fff;
    border: none;
    border-radius: 5px;
    color: #000;
    font-family: Arial;
    font-size: 18px;
    &:focus {
        outline-color: $green;
    }
    &-wrap {
        width: 100%;
        background: $gray;
    }
    &-content {
        width: 100%;
        max-width: 1450px;
        margin: 0 auto;
        padding: 25px;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &-btn {
        padding: 22px 88px;
        border: none;
        border-radius: 10px;
        background: $green;
        color: #fff;
        font-family: Arial;
        font-weight: 700;
        font-size: 30px;
        text-transform: capitalize;
        outline: none;
        height: 100%;
        cursor: pointer;
        &:hover {
            background: darken($green, 10);
            transition: .5s;
        }
    }
}

@media (max-width: 920px) {
    .message-input {
        margin: 0;
        margin-right: 15px;
        &-content {
            padding: 15px;
        }
        &-btn {
            padding: 18px 72px;
            font-size: 26px;
        }
    }
}

@media (max-width: 560px) {
    .message-input {
        margin-right: 10px;
        padding: 15px;
        font-size: 16px;
        &-btn {
            padding: 14px 50px;
            font-size: 20px;
        }
    }
}

@media (max-width: 420px) {
    .message-input {
        padding: 10px;
        font-size: 14px;
        &-content {
            padding: 10px 15px;
        }
        &-btn {
            padding: 10px 35px;
            font-size: 14px;
        }
    }
}